// src/components/ProductSalesChart.js
import React from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';

Chart.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

export default function ProductSalesChart({ products }) {
    if (!products || products.length === 0) {
        return <div className="flex justify-center items-center h-full"><p>No data available for chart.</p></div>;
    }

    const chartData = {
        labels: products.map(p => p.product_name),
        datasets: [
            {
                label: 'Total Sales ()',
                data: products.map(p => p.total_sales),
                backgroundColor: 'rgba(59, 130, 246, 0.5)',
                borderColor: 'rgba(59, 130, 246, 1)',
                borderWidth: 1,
            },
        ],
    };

    const options = {
        indexAxis: 'y', // Makes the bar chart horizontal
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
            legend: {
                display: false,
            },
            title: {
                display: true,
                text: 'Top 20 Products by Sales',
            },
        },
        scales: {
            x: {
                beginAtZero: true,
            },
        },
    };

    return <Bar options={options} data={chartData} />;
}