// src/pages/Products.js
import React, { useState } from 'react';
import DateRangePicker from '../components/DateRangePicker';
import DatePresetButtons from '../components/DatePresetButtons';
import { useApi } from '../hooks/useApi';
import SubNav from '../components/SubNav';
import ProductSalesChart from '../components/ProductSalesChart'; //   
import Papa from 'papaparse';
import { TrendingUp, TrendingDown, Layers, Lock, Download } from 'lucide-react';

const formatDate = (date) => date.toISOString().split('T')[0];

const productReportItems = [
    { id: 'bestsellers', name: 'Best-Sellers', icon: TrendingUp, isLocked: false },
    { id: 'worstsellers', name: 'Worst-Sellers', icon: TrendingDown, isLocked: true },
    { id: 'byvariation', name: 'Sales by Variation', icon: Layers, isLocked: true },
];

// Best-Sellers Report Content Component
const BestSellersReport = ({ products, loading, error, onExport }) => {
    const formatCurrency = (value) => `${parseFloat(value || 0).toLocaleString('en-IN', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`;

    if (loading) return <p className="p-6 text-center">Loading products...</p>;
    if (error) return <p className="p-6 text-center text-red-500">Error: {error}</p>;
    if (!products || products.length === 0) return <p className="p-6 text-center text-gray-500">No products found for the selected date range.</p>;

    return (
        <div>
            {/* Chart */}
            <div className="p-4 border-b border-gray-200 h-96">
                <ProductSalesChart products={products} />
            </div>
            
            {/* Export Button */}
            <div className="p-4 flex justify-end">
                <button
                    onClick={onExport}
                    className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700"
                >
                    <Download className="mr-2 h-4 w-4" />
                    Export to CSV
                </button>
            </div>
            
            {/* Table */}
            <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                        <tr>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Product</th>
                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">SKU</th>
                            <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase">Units Sold</th>
                            <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase">Total Sales</th>
                        </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                        {products.map(product => (
                            <tr key={product.product_id}>
                                <td className="px-6 py-4 whitespace-nowrap">
                                    <div className="flex items-center">
                                        <img className="h-10 w-10 rounded-md object-cover" src={product.image_url || 'https://via.placeholder.com/40'} alt={product.product_name} />
                                        <div className="ml-4">
                                            <a href={product.edit_url} target="_blank" rel="noopener noreferrer" className="text-sm font-medium text-gray-900 hover:text-blue-600">{product.product_name}</a>
                                        </div>
                                    </div>
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{product.sku}</td>
                                <td className="px-6 py-4 whitespace-nowrap text-right text-sm text-gray-500">{product.units_sold}</td>
                                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-semibold text-gray-800">{formatCurrency(product.total_sales)}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

const PremiumFeaturePlaceholder = ({ featureName }) => (
    <div className="p-10 text-center">
        <Lock className="mx-auto h-12 w-12 text-gray-400" />
        <h3 className="mt-2 text-lg font-medium text-gray-900">Unlock {featureName}</h3>
        <p className="mt-1 text-sm text-gray-500">This feature and more are available in the Premium version.</p>
        <div className="mt-6">
            <a href="#" className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700">
                Upgrade to Pro
            </a>
        </div>
    </div>
);

export default function Products() {
    const [startDate, setStartDate] = useState(new Date(new Date().setDate(new Date().getDate() - 29)));
    const [endDate, setEndDate] = useState(new Date());
    const [activeReport, setActiveReport] = useState('bestsellers');

    const endpoint = `products/bestsellers?start_date=${formatDate(startDate)}&end_date=${formatDate(endDate)}`;
    const { data: products, loading, error } = useApi(endpoint);

    const handleExport = () => {
        if (!products || products.length === 0) {
            alert("No data to export.");
            return;
        }
        // We only need specific fields for the CSV
        const dataToExport = products.map(p => ({
            'Product ID': p.product_id,
            'Product Name': p.product_name,
            'SKU': p.sku,
            'Units Sold': p.units_sold,
            'Total Sales': p.total_sales,
        }));
        
        const csv = Papa.unparse(dataToExport);
        const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = `best-selling-products-${formatDate(new Date())}.csv`;
        link.click();
    };

    const renderContent = () => {
        switch (activeReport) {
            case 'bestsellers':
                return <BestSellersReport products={products} loading={loading} error={error} onExport={handleExport} />;
            case 'worstsellers':
                return <PremiumFeaturePlaceholder featureName="Worst-Sellers Report" />;
            case 'byvariation':
                return <PremiumFeaturePlaceholder featureName="Sales by Variation Report" />;
            default:
                return null;
        }
    };

    return (
        <div>
            {/* Filter Section */}
            <div className="p-4 bg-white rounded-xl shadow mb-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 items-end">
                    <div>
                        <label className="text-sm font-medium text-gray-700 block mb-1">Quick Select</label>
                        <DatePresetButtons setStartDate={setStartDate} setEndDate={setEndDate} />
                    </div>
                    <div>
                        <label className="text-sm font-medium text-gray-700 block mb-1">Custom Range</label>
                        <DateRangePicker startDate={startDate} setStartDate={setStartDate} endDate={endDate} setEndDate={setEndDate} />
                    </div>
                </div>
            </div>

            {/* Main Content Area */}
            <div className="main-area">
                <SubNav items={productReportItems} activeItem={activeReport} setActiveItem={setActiveReport} />
                <div className="flex-1 bg-white rounded-xl shadow overflow-hidden">
                    {renderContent()}
                </div>
            </div>
        </div>
    );
}