// src/components/SalesChart.js
import React from 'react';
import { Line } from 'react-chartjs-2';
import { Chart, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler } from 'chart.js';
import { useApi } from '../hooks/useApi';

Chart.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler);

export default function SalesChart({ startDate, endDate }) {
    const formatDate = (date) => date.toISOString().split('T')[0];
    const endpoint = `chart/sales-over-time?start_date=${formatDate(startDate)}&end_date=${formatDate(endDate)}`;
    
    const { data: chartData, loading, error } = useApi(endpoint);

    const options = {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
            legend: {
                display: false,
            },
        },
        scales: {
            y: {
                beginAtZero: true,
            },
        },
    };

    if (loading) return <div className="flex justify-center items-center h-full"><p>Loading Chart...</p></div>;
    if (error) return <div className="flex justify-center items-center h-full text-red-500"><p>Error loading chart data.</p></div>;

    return chartData ? <Line options={options} data={chartData} /> : null;
}