// src/index.js
import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App'; // Admin App
import CustomerApp from './CustomerApp'; //  Customer App

const adminRootElement = document.getElementById('aorw-react-app-root');
const customerRootElement = document.getElementById('aorw-customer-app-root');

if (adminRootElement) {
  const root = ReactDOM.createRoot(adminRootElement);
  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
} else if (customerRootElement) {
  const root = ReactDOM.createRoot(customerRootElement);
  root.render(
    <React.StrictMode>
      <CustomerApp />
    </React.StrictMode>
  );
}