import React from 'react';
import { Lock } from 'lucide-react'; //    Lock 

export default function SubNav({ items, activeItem, setActiveItem }) {
    return (
        <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-6 overflow-x-auto" aria-label="Tabs">
                {items.map(item => (
                    <button
                        key={item.name}
                        onClick={() => !item.isLocked && setActiveItem(item.id)}
                        disabled={item.isLocked}
                        className={`group whitespace-nowrap inline-flex items-center py-4 px-1 border-b-2 font-medium text-sm transition-colors duration-200 ease-in-out ${
                            activeItem === item.id
                                ? 'border-blue-500 text-blue-600' // Active tab style
                                : item.isLocked
                                ? 'border-transparent text-gray-400 cursor-not-allowed' // Locked tab style
                                : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300' // Inactive tab style
                        }`}
                        aria-current={activeItem === item.id ? 'page' : undefined}
                    >
                        <item.icon className={`mr-2 h-5 w-5 ${
                            activeItem === item.id 
                                ? 'text-blue-500' 
                                : item.isLocked
                                ? 'text-gray-400'
                                : 'text-gray-400 group-hover:text-gray-500'
                            }`} 
                        />
                        <span>{item.name}</span>
                        {item.isLocked && <Lock className="ml-2 h-4 w-4 text-gray-400" />}
                    </button>
                ))}
            </nav>
        </div>
    );
}