// src/pages/Gateways.js
import React, { useState } from 'react';
import DateRangePicker from '../components/DateRangePicker';
import DatePresetButtons from '../components/DatePresetButtons';
import { useApi } from '../hooks/useApi';
import { Pie } from 'react-chartjs-2';
import { Chart, ArcElement, Tooltip, Legend } from 'chart.js';
import Papa from 'papaparse';
import { Download } from 'lucide-react';

Chart.register(ArcElement, Tooltip, Legend);

const formatDate = (date) => date.toISOString().split('T')[0];

export default function Gateways() {
    const [startDate, setStartDate] = useState(new Date(new Date().setDate(new Date().getDate() - 29)));
    const [endDate, setEndDate] = useState(new Date());

    const endpoint = `gateways?start_date=${formatDate(startDate)}&end_date=${formatDate(endDate)}`;
    const { data: gateways, loading, error } = useApi(endpoint);

    const formatCurrency = (value) => `${parseFloat(value || 0).toLocaleString('en-IN', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`;

    // Chart.js-    
    const chartData = {
        labels: gateways?.map(g => g.title) || [],
        datasets: [{
            data: gateways?.map(g => g.total_sales) || [],
            backgroundColor: [
                'rgba(59, 130, 246, 0.7)', 'rgba(239, 68, 68, 0.7)',
                'rgba(22, 163, 74, 0.7)', 'rgba(245, 158, 11, 0.7)',
                'rgba(107, 114, 128, 0.7)', 'rgba(139, 92, 246, 0.7)',
            ],
        }],
    };
    
    const chartOptions = {
        responsive: true,
        maintainAspectRatio: false,
        plugins: { legend: { position: 'right' } },
    };

    const handleExport = () => {
        if (!gateways || gateways.length === 0) {
            alert("No data to export."); return;
        }
        const csv = Papa.unparse(gateways.map(g => ({
            'Gateway': g.title,
            'Order Count': g.order_count,
            'Total Sales': g.total_sales,
        })));
        const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = `payment-gateways-report-${formatDate(new Date())}.csv`;
        link.click();
    };

    return (
        <div>
            {/* Filter Section */}
            <div className="p-4 bg-white rounded-xl shadow mb-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 items-end">
                    <div>
                        <label className="text-sm font-medium text-gray-700 block mb-1">Quick Select</label>
                        <DatePresetButtons setStartDate={setStartDate} setEndDate={setEndDate} />
                    </div>
                    <div>
                        <label className="text-sm font-medium text-gray-700 block mb-1">Custom Range</label>
                        <DateRangePicker startDate={startDate} setStartDate={setEndDate} endDate={endDate} setEndDate={setEndDate} />
                    </div>
                </div>
            </div>
            
            <div className="bg-white rounded-xl shadow">
                <div className="p-4 sm:p-6 flex justify-between items-center border-b border-gray-200">
                    <h2 className="text-xl font-semibold text-gray-800">Payment Gateways Report</h2>
                    <button onClick={handleExport} disabled={!gateways || gateways.length === 0} className="inline-flex items-center px-3 py-1.5 border border-transparent text-xs font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 disabled:opacity-50">
                        <Download className="mr-2 h-4 w-4" />
                        Export
                    </button>
                </div>

                {loading && <p className="p-6 text-center">Loading report...</p>}
                {error && <p className="p-6 text-center text-red-500">Error: {error}</p>}
                
                {!loading && !error && (!gateways || gateways.length === 0) && <p className="p-6 text-center text-gray-500">No payment data found.</p>}
                
                {!loading && !error && gateways && gateways.length > 0 && (
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 p-6">
                        <div className="h-96">
                            <Pie data={chartData} options={chartOptions} />
                        </div>
                        <div className="overflow-x-auto border rounded-lg">
                            <table className="min-w-full divide-y divide-gray-200">
                                <thead className="bg-gray-50">
                                    <tr>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Gateway</th>
                                        <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase">Order Count</th>
                                        <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase">Total Sales</th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white divide-y divide-gray-200">
                                    {gateways.map(gateway => (
                                        <tr key={gateway.id}>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{gateway.title}</td>
                                            <td className="px-6 py-4 whitespace-nowrap text-right text-sm text-gray-500">{gateway.order_count.toLocaleString('en-IN')}</td>
                                            <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-semibold text-gray-800">{formatCurrency(gateway.total_sales)}</td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}