import React, { useState } from 'react';
import DateRangePicker from '../components/DateRangePicker';
import DatePresetButtons from '../components/DatePresetButtons';
import { useApi } from '../hooks/useApi';
import SubNav from '../components/SubNav';
import { Bar } from 'react-chartjs-2';
import { Chart, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import Papa from 'papaparse';
import { List, GitMerge, Percent, Lock, Download } from 'lucide-react';

// Chart.js      
Chart.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

//  'YYYY-MM-DD'     
const formatDate = (date) => date.toISOString().split('T')[0];

// -  
const categoryReportItems = [
    { id: 'bestsellers', name: 'Top Categories', icon: List, isLocked: false },
    { id: 'subcategories', name: 'Sub-Category Report', icon: GitMerge, isLocked: true },
    { id: 'profitability', name: 'Category Profitability', icon: Percent, isLocked: true },
];

// ####################################################################
// #                #
// ####################################################################
const BestSellingCategoriesReport = ({ categories, loading, error, onExport }) => {
    const formatCurrency = (value) => `${parseFloat(value || 0).toLocaleString('en-IN', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`;

    if (loading) return <p className="p-6 text-center">Loading categories...</p>;
    if (error) return <p className="p-6 text-center text-red-500">Error: {error}</p>;
    if (!categories || categories.length === 0) return <p className="p-6 text-center text-gray-500">No category sales data found for the selected date range.</p>;

    //     
    const chartData = {
        labels: categories.map(c => c.category_name),
        datasets: [{
            label: 'Total Sales',
            data: categories.map(c => c.total_sales),
            backgroundColor: 'rgba(22, 163, 74, 0.6)',
            borderColor: 'rgba(22, 163, 74, 1)',
            borderWidth: 1,
        }],
    };
    
    const chartOptions = {
        indexAxis: 'y',
        responsive: true,
        maintainAspectRatio: false,
        plugins: { legend: { display: false } },
        scales: {
            x: {
                ticks: {
                    callback: function(value) {
                        if (value >= 1000) {
                            return '' + value / 1000 + 'k';
                        }
                        return '' + value;
                    }
                }
            }
        }
    };

    return (
        <div>
            <div className="p-4 flex justify-between items-center border-b border-gray-200">
                <h3 className="font-semibold text-gray-800">Top 10 Categories by Sales</h3>
                <button
                    onClick={onExport}
                    className="inline-flex items-center px-3 py-1.5 border border-transparent text-xs font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700"
                >
                    <Download className="mr-2 h-4 w-4" />
                    Export
                </button>
            </div>
            
            <div className="grid grid-cols-1 lg:grid-cols-5 gap-6 p-6">
                <div className="lg:col-span-2 h-96">
                    <Bar data={chartData} options={chartOptions} />
                </div>
                
                <div className="lg:col-span-3">
                    <div className="overflow-x-auto border rounded-lg">
                        <table className="min-w-full divide-y divide-gray-200">
                            <thead className="bg-gray-50">
                                <tr>
                                    <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Category</th>
                                    <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Units Sold</th>
                                    <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Total Sales</th>
                                </tr>
                            </thead>
                            <tbody className="bg-white divide-y divide-gray-200">
                                {categories.map(cat => (
                                    <tr key={cat.category_id}>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{cat.category_name}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-right text-sm text-gray-500">{cat.units_sold.toLocaleString('en-IN')}</td>
                                        <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-semibold text-gray-800">{formatCurrency(cat.total_sales)}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    );
};


// ################################################################
// #                #
// ################################################################
const PremiumFeaturePlaceholder = ({ featureName }) => (
    <div className="p-10 text-center flex flex-col items-center justify-center h-full">
        <Lock className="h-12 w-12 text-gray-400" />
        <h3 className="mt-2 text-lg font-medium text-gray-900">Unlock {featureName}</h3>
        <p className="mt-1 text-sm text-gray-500">This feature is available in the Premium version.</p>
        <a href="#" className="mt-6 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700">
            Upgrade to Pro
        </a>
    </div>
);


// ###############################################
// #  Categories                   #
// ###############################################
export default function Categories() {
    const [startDate, setStartDate] = useState(new Date(new Date().setDate(new Date().getDate() - 29)));
    const [endDate, setEndDate] = useState(new Date());
    const [activeReport, setActiveReport] = useState('bestsellers');

    const endpoint = `categories/bestsellers?start_date=${formatDate(startDate)}&end_date=${formatDate(endDate)}`;
    const { data: categories, loading, error } = useApi(endpoint);

    const handleExport = () => {
        if (!categories || categories.length === 0) {
            alert("No data to export.");
            return;
        }
        const dataToExport = categories.map(cat => ({
            'Category ID': cat.category_id,
            'Category Name': cat.category_name,
            'Units Sold': cat.units_sold,
            'Total Sales': cat.total_sales,
        }));
        
        const csv = Papa.unparse(dataToExport);
        const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
        const link = document.createElement('a');
        link.href = URL.createObjectURL(blob);
        link.download = `top-categories-report-${formatDate(new Date())}.csv`;
        link.click();
    };

    const renderContent = () => {
        switch (activeReport) {
            case 'bestsellers':
                return <BestSellingCategoriesReport categories={categories} loading={loading} error={error} onExport={handleExport} />;
            case 'subcategories':
                return <PremiumFeaturePlaceholder featureName="Sub-Category Report" />;
            case 'profitability':
                return <PremiumFeaturePlaceholder featureName="Category Profitability" />;
            default:
                return null;
        }
    };

    return (
        <div>
            {/* Filter Section */}
            <div className="p-4 bg-white rounded-xl shadow mb-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 items-end">
                    <div>
                        <label className="text-sm font-medium text-gray-700 block mb-1">Quick Select</label>
                        <DatePresetButtons setStartDate={setStartDate} setEndDate={setEndDate} />
                    </div>
                    <div>
                        <label className="text-sm font-medium text-gray-700 block mb-1">Custom Range</label>
                        <DateRangePicker startDate={startDate} setStartDate={setEndDate} endDate={endDate} setEndDate={setEndDate} />
                    </div>
                </div>
            </div>

            {/* Main Content Area */}
            <div className="main-div">
                <SubNav items={categoryReportItems} activeItem={activeReport} setActiveItem={setActiveReport} />
                <div className="flex-1 bg-white rounded-xl shadow overflow-hidden">
                    {renderContent()}
                </div>
            </div>
        </div>
    );
}