// src/components/MetricCard.js
import React from 'react';

export default function MetricCard({ title, value, icon, change, changeType }) {
    const Icon = icon;
    const changeColor = changeType === 'increase' ? 'text-green-600' : 'text-red-600';

    return (
        <div className="bg-white p-5 rounded-xl shadow">
            <div className="flex items-center">
                <div className="flex-shrink-0">
                    <div className="bg-blue-100 text-blue-600 p-3 rounded-lg">
                        <Icon className="h-6 w-6" />
                    </div>
                </div>
                <div className="ml-5 w-0 flex-1">
                    <dl>
                        <dt className="text-sm font-medium text-gray-500 truncate">{title}</dt>
                        <dd className="flex items-baseline">
                            <p className="text-2xl font-semibold text-gray-900">{value}</p>
                            {change && (
                                <p className={`ml-2 flex items-baseline text-sm font-semibold ${changeColor}`}>
                                    {changeType === 'increase' ? '' : ''}
                                    <span className="sr-only">{changeType === 'increase' ? 'Increased' : 'Decreased'} by</span>
                                    {change}
                                </p>
                            )}
                        </dd>
                    </dl>
                </div>
            </div>
        </div>
    );
}