// src/hooks/useApi.js
import { useState, useEffect, useCallback } from 'react';

// Get the global settings object passed from WordPress via wp_localize_script
const apiSettings = window.aorwApiSettings;

export function useApi(endpoint) {
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    const fetchData = useCallback(async () => {
        // CRITICAL FIX: Check if apiSettings is available before making a call.
        if (!apiSettings || !apiSettings.root || !apiSettings.nonce) {
            setError("API settings are missing. Cannot connect to WordPress.");
            setLoading(false);
            return;
        }

        setLoading(true);
        setError(null);
        try {
            const response = await fetch(`${apiSettings.root}${endpoint}`, {
                headers: {
                    'X-WP-Nonce': apiSettings.nonce,
                },
            });

            if (!response.ok) {
                // Try to parse error message from the server for better debugging
                const errorBody = await response.json().catch(() => ({ message: `HTTP error! Status: ${response.status}` }));
                throw new Error(errorBody.message || `An unknown error occurred.`);
            }

            const result = await response.json();
            setData(result);
        } catch (e) {
            setError(e.message);
        } finally {
            setLoading(false);
        }
    }, [endpoint]);

    useEffect(() => {
        fetchData();
    }, [fetchData]);

    return { data, loading, error, refetch: fetchData };
}