// src/pages/MyOrdersContent.js
import React, { useState, useEffect } from 'react';
import { useApi } from '../hooks/useApi';
import { Search } from 'lucide-react';

// Status badge component for consistent styling
const StatusBadge = ({ status }) => {
    const statusClasses = {
        'processing': 'bg-blue-100 text-blue-800',
        'completed': 'bg-green-100 text-green-800',
        'on-hold': 'bg-yellow-100 text-yellow-800',
        'failed': 'bg-red-100 text-red-800',
        'cancelled': 'bg-gray-100 text-gray-800',
        'refunded': 'bg-purple-100 text-purple-800',
        'pending': 'bg-gray-100 text-gray-800',
    };
    return (
        <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${statusClasses[status] || 'bg-gray-100 text-gray-800'}`}>
            {status.replace('-', ' ')}
        </span>
    );
};

export default function MyOrdersContent() {
    const [page, setPage] = useState(1);
    const [status, setStatus] = useState('any');
    const [search, setSearch] = useState('');
    const [searchTerm, setSearchTerm] = useState('');

    const endpoint = `customer/orders?page=${page}&status=${status}&search=${searchTerm}`;
    const { data, loading, error, refetch } = useApi(endpoint);

    useEffect(() => {
        const handler = setTimeout(() => {
            setSearchTerm(search);
            setPage(1); // Reset to first page on new search
        }, 500); // Debounce search input
        return () => clearTimeout(handler);
    }, [search]);

    useEffect(() => {
        refetch();
    }, [page, status, searchTerm, refetch]);

    const orderStatuses = ['any', 'processing', 'completed', 'on-hold', 'failed', 'cancelled', 'refunded', 'pending'];

    return (
        <div className="space-y-6">
            {/* Filters and Search */}
            <div className="flex flex-col sm:flex-row gap-4">
                <div className="relative flex-grow">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                        <Search className="h-5 w-5 text-gray-400" />
                    </div>
                    <input
                        type="text"
                        placeholder="Search by Order ID..."
                        value={search}
                        onChange={e => setSearch(e.target.value)}
                        className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md"
                    />
                </div>
                <select onChange={e => { setStatus(e.target.value); setPage(1); }} className="border border-gray-300 rounded-md">
                    {orderStatuses.map(s => <option key={s} value={s}>{s.charAt(0).toUpperCase() + s.slice(1).replace('-', ' ')}</option>)}
                </select>
            </div>
            
            {/* Orders List */}
            {loading && <p>Loading orders...</p>}
            {error && <p className="text-red-500">Could not load orders: {error}</p>}
            
            {!loading && data?.orders && (
                <div className="bg-white p-5 rounded-lg shadow-sm border border-gray-100 space-y-4">
                    {data.orders.map(order => (
                        <div key={order.id} className="border-b pb-4 last:border-b-0">
                            <div className="flex justify-between items-center">
                                <div>
                                    <a href={order.view_url} className="text-lg font-semibold text-blue-600">Order #{order.order_number}</a>
                                    <p className="text-sm text-gray-500">{order.date_created}</p>
                                </div>
                                <div className="text-right">
                                    <StatusBadge status={order.status} />
                                    <p 
                                        className="font-bold text-lg" 
                                        dangerouslySetInnerHTML={{ __html: order.total }} 
                                    />
                                </div>
                            </div>
                            <div className="mt-4 flex space-x-4">
                                {order.items.slice(0, 5).map((item, index) => (
                                    <img key={index} src={item.image} alt={item.name} className="h-12 w-12 rounded-md object-cover" title={`${item.name} (Qty: ${item.quantity})`} />
                                ))}
                                {order.items.length > 5 && <div className="h-12 w-12 rounded-md bg-gray-100 flex items-center justify-center text-sm text-gray-500">+{order.items.length - 5}</div>}
                            </div>
                        </div>
                    ))}
                </div>
            )}
            
            {/* Pagination */}
            {!loading && data?.totalPages > 1 && (
                <div className="flex justify-between items-center">
                    <button onClick={() => setPage(p => Math.max(1, p - 1))} disabled={page === 1} className="px-4 py-2 text-sm rounded-md bg-white border disabled:opacity-50">Previous</button>
                    <span>Page {page} of {data.totalPages}</span>
                    <button onClick={() => setPage(p => Math.min(data.totalPages, p + 1))} disabled={page === data.totalPages} className="px-4 py-2 text-sm rounded-md bg-white border disabled:opacity-50">Next</button>
                </div>
            )}
        </div>
    );
}