// src/components/NavTabs.js
import React from 'react';
import { NavLink } from 'react-router-dom';
import { LayoutDashboard, ShoppingCart, Package, Folder, CreditCard } from 'lucide-react';

const menuItems = [
    { path: '/', name: 'Overview', icon: LayoutDashboard },
    { path: '/orders', name: 'Orders', icon: ShoppingCart },
    { path: '/products', name: 'Products', icon: Package },
    { path: '/categories', name: 'Categories', icon: Folder },
    { path: '/gateways', name: 'Payment Gateways', icon: CreditCard },
];

export default function NavTabs() {
    const baseClasses = "inline-flex items-center justify-center px-4 py-2.5 text-sm font-medium rounded-md border border-transparent";
    const activeClasses = "bg-blue-600 text-white shadow-sm";
    const inactiveClasses = "text-gray-500 hover:bg-gray-100 hover:text-gray-700";

    return (
        <div className="mb-6">
            <nav className="flex space-x-2 bg-white p-1.5 rounded-lg shadow-sm">
                {menuItems.map(item => {
                    const Icon = item.icon;
                    return (
                        <NavLink
                            key={item.name}
                            to={item.path}
                            className={({ isActive }) => `${baseClasses} ${isActive ? activeClasses : inactiveClasses}`}
                        >
                            <Icon className="mr-2 h-4 w-4" />
                            {item.name}
                        </NavLink>
                    );
                })}
            </nav>
        </div>
    );
}