// src/pages/Orders.js
import React, { useState, useEffect, useCallback } from 'react';
import DateRangePicker from '../components/DateRangePicker';
import { useApi } from '../hooks/useApi';
import Papa from 'papaparse';
import { Download } from 'lucide-react'; 
import DatePresetButtons from '../components/DatePresetButtons';
const formatDate = (date) => date.toISOString().split('T')[0];

export default function Orders() {
    const [startDate, setStartDate] = useState(new Date(new Date().setDate(new Date().getDate() - 29)));
    const [endDate, setEndDate] = useState(new Date());
    const [currentPage, setCurrentPage] = useState(1);
    const [status, setStatus] = useState('any'); // Order status filter

    const [orders, setOrders] = useState([]);
    const [totalPages, setTotalPages] = useState(1);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    const [isExporting, setIsExporting] = useState(false);
    const apiSettings = window.aorwApiSettings || {};

    const handleExport = async () => {
        setIsExporting(true);
        const endpoint = `export/orders?start_date=${formatDate(startDate)}&end_date=${formatDate(endDate)}&status=${status}`;
        try {
            const response = await fetch(`${apiSettings.root}${endpoint}`, {
                headers: { 'X-WP-Nonce': apiSettings.nonce },
            });
            if (!response.ok) throw new Error('Failed to fetch data for export');
            const dataToExport = await response.json();
            
            const csv = Papa.unparse(dataToExport);
            const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
            const link = document.createElement('a');
            const url = URL.createObjectURL(blob);
            link.setAttribute('href', url);
            link.setAttribute('download', `orders-report-${formatDate(new Date())}.csv`);
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            
        } catch (err) {
            console.error("Export failed:", err);
            alert("Could not export data. Please try again.");
        } finally {
            setIsExporting(false);
        }
    };

    const fetchOrders = useCallback(async () => {
        setLoading(true);
        setError(null);
        const endpoint = `orders?start_date=${formatDate(startDate)}&end_date=${formatDate(endDate)}&page=${currentPage}&status=${status}`;
        try {
            const response = await fetch(`${apiSettings.root}${endpoint}`, {
                headers: { 'X-WP-Nonce': apiSettings.nonce },
            });
            if (!response.ok) throw new Error('Failed to fetch orders');
            
            // Get pagination data from headers
            setTotalPages(parseInt(response.headers.get('X-WP-TotalPages') || 1, 10));
            
            const result = await response.json();
            setOrders(result);
        } catch (e) {
            setError(e.message);
        } finally {
            setLoading(false);
        }
    }, [startDate, endDate, currentPage, status, apiSettings.root, apiSettings.nonce]);

    useEffect(() => {
        fetchOrders();
    }, [fetchOrders]);
    
    // WooCommerce order statuses
    const orderStatuses = {
        'any': 'All Statuses',
        'wc-pending': 'Pending payment',
        'wc-processing': 'Processing',
        'wc-on-hold': 'On hold',
        'wc-completed': 'Completed',
        'wc-cancelled': 'Cancelled',
        'wc-refunded': 'Refunded',
        'wc-failed': 'Failed',
    };

    return (
        <div>
            
            <div className="p-4 bg-white rounded-xl shadow mb-6">
                <div className="grid grid-cols-1 md:grid-cols-1 gap-4">
                    <div>
                        <label className="text-sm font-medium text-gray-700">Quick Select</label>
                        <DatePresetButtons setStartDate={setStartDate} setEndDate={setEndDate} />
                    </div>
                    <div className="flex items-end gap-4">
                        <div className="flex-1">
                            <label className="text-sm font-medium text-gray-700">Custom Range</label>
                            <DateRangePicker startDate={startDate} setStartDate={setStartDate} endDate={endDate} setEndDate={setEndDate} />
                        </div>
                        <div>
                            <label className="text-sm font-medium text-gray-700">Status</label>
                            <select
                                value={status}
                                onChange={(e) => { setStatus(e.target.value); setCurrentPage(1); }}
                                className="w-full rounded-md border-gray-300 shadow-sm"
                            >
                                {Object.entries(orderStatuses).map(([key, value]) => (
                                    <option key={key} value={key}>{value}</option>
                                ))}
                            </select>
                        </div>
                    </div>
                </div>
            </div>

            <div className="bg-white p-4 sm:p-6 rounded-xl shadow">
                <div className="flex justify-end mb-4">
                    <button
                        onClick={handleExport}
                        disabled={isExporting}
                        className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 disabled:opacity-50"
                    >
                        <Download className="mr-2 h-4 w-4" />
                        {isExporting ? 'Exporting...' : 'Export to CSV'}
                    </button>
                </div>
                {loading && <p>Loading orders...</p>}
                {error && <p className="text-red-500">Error: {error}</p>}
                
                {!loading && !error && (
                    <>
                        <div className="overflow-x-auto">
                            <table className="min-w-full divide-y divide-gray-200">
                                <thead className="bg-gray-50">
                                    <tr>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Order</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Date</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Customer</th>
                                        <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Total</th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white divide-y divide-gray-200">
                                    {orders.map(order => (
                                        <tr key={order.id}>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-blue-600">
                                                <a href={order.edit_url} target="_blank" rel="noopener noreferrer">#{order.order_number}</a>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{order.date_created}</td>
                                            <td className="px-6 py-4 whitespace-nowrap">
                                                <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800`}>
                                                    {order.status}
                                                </span>
                                            </td>
                                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{order.customer_name}</td>
                                            <td className="px-6 py-4 whitespace-nowrap text-right text-sm text-gray-500">
                                                {`${order.total.toLocaleString('en-IN')}`}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                        {/* Pagination Controls */}
                        <div className="flex justify-between items-center mt-4">
                            <button onClick={() => setCurrentPage(p => Math.max(1, p - 1))} disabled={currentPage === 1} className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md disabled:opacity-50">Previous</button>
                            <span>Page {currentPage} of {totalPages}</span>
                            <button onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))} disabled={currentPage === totalPages} className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md disabled:opacity-50">Next</button>
                        </div>
                    </>
                )}
            </div>
        </div>
    );
}