import React, { useState } from 'react';
import { useApi } from '../hooks/useApi';
import SubNav from '../components/SubNav';
import { LayoutDashboard, ShoppingCart, BarChart, Heart, Lock, Download, DollarSign, Package, TrendingUp } from 'lucide-react';
import { Bar } from 'react-chartjs-2';
import { Chart, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import Papa from 'papaparse';
import MyOrdersContent from './MyOrdersContent';

// Chart.js   
Chart.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

// -  
const customerMenuItems = (isPremium) => [
    { id: 'overview', name: 'My Dashboard', icon: LayoutDashboard, isLocked: false },
    { id: 'orders', name: 'My Orders', icon: ShoppingCart, isLocked: false },
    { id: 'spending_by_category', name: 'Spending by Category', icon: BarChart, isLocked: !isPremium },
    { id: 'wishlist', name: 'My Wishlist', icon: Heart, isLocked: !isPremium },
];

//   
const CustomerMetricCard = ({ title, value, icon }) => {
    const Icon = icon;
    return (
        <div className="bg-white p-5 rounded-xl shadow-sm border border-gray-200 flex flex-col justify-between">
            <div>
                <div className="flex justify-between items-start">
                    <p className="text-sm font-medium text-gray-500">{title}</p>
                    <div className="bg-blue-100 text-blue-600 p-2 rounded-lg">
                        <Icon className="h-5 w-5" />
                    </div>
                </div>
            </div>
            <div className="mt-2">
                <p className="text-3xl font-bold text-gray-900 whitespace-nowrap overflow-hidden text-ellipsis">{value}</p>
            </div>
        </div>
    );
};

//    
const SpendingChart = () => {
    const { data: chartApiData, loading, error } = useApi('customer/spending-chart');
    
    if (loading) return <div className="h-full flex items-center justify-center"><p className="text-gray-500">Loading chart data...</p></div>;
    if (error) return <div className="h-full flex items-center justify-center"><p className="text-red-500">Could not load chart.</p></div>;
    if (!chartApiData) return null;
    
    const chartData = {
        labels: chartApiData.labels,
        datasets: [{
            label: 'Monthly Spending ()',
            data: chartApiData.data,
            backgroundColor: 'rgba(59, 130, 246, 0.5)',
            borderColor: 'rgba(59, 130, 246, 1)',
            borderWidth: 1,
            borderRadius: 4,
        }],
    };
    
    const chartOptions = { 
        responsive: true, 
        maintainAspectRatio: false,
        plugins: { legend: { display: false } },
        scales: { y: { beginAtZero: true } }
    };

    return <Bar data={chartData} options={chartOptions} />;
};

//    ( )
const OverviewContent = ({ data, loading, error, onExport }) => {
    if (loading) return <div className="p-4"><p>Loading your dashboard...</p></div>;
    if (error) return <div className="p-4 text-red-500"><p>Could not load dashboard data: {error}</p></div>;
    if (!data) return null;

    const formatCurrency = (value) => `${parseFloat(value || 0).toLocaleString('en-IN')}`;

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between sm:items-center gap-4">
                 <h3 className="text-xl font-semibold text-gray-800">Your Purchase Summary</h3>
                 <button 
                    onClick={onExport} 
                    className="inline-flex items-center justify-center px-4 py-2 text-sm font-medium rounded-md text-blue-700 bg-blue-100 hover:bg-blue-200 transition-colors"
                 >
                     <Download className="mr-2 h-4 w-4" />
                     Export My Orders
                 </button>
            </div>
            <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4">
                <CustomerMetricCard title="Total Spent" value={formatCurrency(data.total_spent)} icon={DollarSign} />
                <CustomerMetricCard title="Total Orders" value={data.order_count} icon={ShoppingCart} />
                <CustomerMetricCard title="Avg. Order Value" value={formatCurrency(data.avg_order_value)} icon={TrendingUp} />
                <CustomerMetricCard title="First Order Date" value={data.first_order_date} icon={Package} />
            </div>
            <div className="bg-white p-5 rounded-lg shadow-sm border border-gray-100 h-80">
                <h3 className="font-medium text-gray-800 mb-4">Your Spending (Last 12 Months)</h3>
                <SpendingChart />
            </div>
        </div>
    );
};

//   
const PremiumFeaturePlaceholder = ({ featureName }) => (
    <div className="p-10 text-center bg-gray-50 rounded-lg">
        <Lock className="mx-auto h-12 w-12 text-gray-400" />
        <h3 className="mt-2 text-lg font-medium text-gray-900">Unlock {featureName}</h3>
        <p className="mt-1 text-sm text-gray-500">This feature is available in the Premium version.</p>
        <a href="#" className="mt-6 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700">
            Learn More
        </a>
    </div>
);


//  CustomerDashboard 
export default function CustomerDashboard() {
    const [activeTab, setActiveTab] = useState('overview');
    const { data, loading, error } = useApi('customer/overview');
    
    const handleExport = async () => {
        const apiSettings = window.aorwApiSettings;
        if (!apiSettings) {
            alert('API settings not found.');
            return;
        }
        
        try {
            const response = await fetch(`${apiSettings.root}customer/export-orders`, { headers: { 'X-WP-Nonce': apiSettings.nonce } });
            if (!response.ok) throw new Error('Export failed');
            
            const dataToExport = await response.json();
            if (dataToExport.length === 0) {
                alert('You have no orders to export.');
                return;
            }

            const csv = Papa.unparse(dataToExport);
            const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
            const link = document.createElement('a');
            link.href = URL.createObjectURL(blob);
            link.download = `my-orders-export.csv`;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        } catch (e) {
            alert('An error occurred during export.');
            console.error(e);
        }
    };

    const renderContent = () => {
        switch (activeTab) {
            case 'overview':
                return <OverviewContent data={data} loading={loading} error={error} onExport={handleExport} />;
            case 'orders':
                return <MyOrdersContent />;
            case 'spending_by_category':
                return <PremiumFeaturePlaceholder featureName="Spending by Category Report" />;
            case 'wishlist':
                return <PremiumFeaturePlaceholder featureName="Your Personal Wishlist" />;
            default:
                return null;
        }
    };

    return (
        <div className="aorw-customer-dashboard bg-gray-50 p-4 sm:p-6 rounded-lg shadow-inner">
            <h2 className="text-3xl font-bold text-gray-900">My Reports</h2>
            <p className="mt-1 text-gray-600 mb-6">Here's a summary of your activity and purchases.</p>
            
            <SubNav items={customerMenuItems(data?.is_premium || false)} activeItem={activeTab} setActiveItem={setActiveTab} />
            
            <div className="mt-8">
                {error && !data ? <p className="p-4 bg-red-100 text-red-700 rounded-md">Could not load your report data. Please try again later.</p> : renderContent()}
            </div>
        </div>
    );
}