// src/components/DatePresetButtons.js
import React from 'react';
import { subDays, startOfMonth, endOfMonth, startOfWeek, endOfWeek, startOfQuarter, endOfQuarter,subYears, startOfYear, endOfYear } from 'date-fns';

const presets = [
    { label: 'Today', range: () => [new Date(), new Date()] },
    { label: 'Yesterday', range: () => [subDays(new Date(), 1), subDays(new Date(), 1)] },
    { label: 'Last 7 Days', range: () => [subDays(new Date(), 6), new Date()] },
    { label: 'Last 15 Days', range: () => [subDays(new Date(), 14), new Date()] },
    { label: 'Last 30 Days', range: () => [subDays(new Date(), 29), new Date()] },
    { label: 'This Month', range: () => [startOfMonth(new Date()), endOfMonth(new Date())] },
    { label: 'Last Month', range: () => {
        const lastMonth = subDays(new Date(), 30);
        return [startOfMonth(lastMonth), endOfMonth(lastMonth)];
    }},
    { label: 'This Year', range: () => [startOfYear(new Date()), endOfYear(new Date())] },
    { label: 'Last Year', range: () => {
        const lastYearStart = startOfYear(subYears(new Date(), 1));
        return [lastYearStart, endOfYear(lastYearStart)];
    }},
];

export default function DatePresetButtons({ setStartDate, setEndDate }) {
    const applyPreset = (preset) => {
        const [start, end] = preset.range();
        setStartDate(start);
        setEndDate(end);
    };

    return (
        <div className="flex flex-wrap gap-2">
            {presets.map(preset => (
                <button
                    key={preset.label}
                    onClick={() => applyPreset(preset)}
                    className="px-3 py-1 text-xs font-medium text-gray-600 bg-gray-100 rounded-md hover:bg-gray-200"
                >
                    {preset.label}
                </button>
            ))}
        </div>
    );
}